@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap");

* {
  padding: 0;
  margin: 0;
}

.body {
  background: rgb(241, 241, 241);
}

.view-wrapper {
  padding-top: 30px;
  padding-right: 30px;
  display: flex;
  align-content: center;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.tickets-list {
  padding-top: 30px;
  position: relative;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  row-gap: 40px;
  justify-items: center;
  padding-bottom: 30px;
}

.ticket-wrapper {
  width: 240px;
  height: 280px;
  border-radius: 10px;
  background-color: rgb(56, 116, 203);
  z-index: 0;
  transition: all 500ms ease-in-out;
  font-family: "Source Sans Pro", sans-serif;
  color: rgb(33, 36, 39);
}

.ticket-wrapper:hover {
  padding-bottom: 10px;
  transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
}

.ticket-info-wrapper {
  width: 230px;
  height: 280px;
  margin-left: 10px;
  z-index: 1;
  border-radius: 0px 10px 10px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 6px 6px 12px rgb(0 0 0 / 50%);
  background: rgba(255, 255, 255);
  overflow: hidden;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  transition: all 500ms ease-in-out;
}

.ticket-info-wrapper:hover {
  border-radius: 10px 10px 10px 10px;
  transition: all 500ms cubic-bezier(0.19, 1, 0.22, 1);
  transform: translateY(-10px);
}

.ticket-info {
  width: 230px;
  display: inline-flex;
  height: 30px;
  padding-top: 10px;
  padding-left: 20px;
}

.ticket-title {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 80%;
  height: 50px;
  box-shadow: 6px 6px 12px rgb(0 0 0 / 50%);
  border-radius: 10px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  transition: all 200ms ease-in-out;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

.ticket-title h3 {
  width: 100%;
}

.ticket-title:hover {
  box-shadow: 9px 9px 12px rgb(0 0 0 / 50%);
  transition: all 200ms ease-in-out;
}

.ticket-info-title {
  font-weight: bold;
}

.ticket-info-value {
  padding-left: 10px;
}

.grid-view-wrapper {
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 30px;
}

@media (max-width: 1400px) {
  .tickets-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 1200px) {
  .tickets-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 900px) {
  .tickets-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 700px) {
  .tickets-list {
    grid-template-columns: repeat(1, 1fr);
  }
  .ticket-wrapper {
    width: 350px;
    height: 400px;
  }
  .ticket-info-wrapper {
    width: 340px;
    height: 400px;
  }
  .ticket-info {
    font-size: 20px;
  }
  .ticket-title {
    font-size: 25px;
  }
  .view-wrapper {
    display: none;
   }
}


